.textarea, .input {
  background-color: hsl(0deg, 0%, 100%);
  border-color: hsl(0deg, 0%, 86%);
  border-radius: 4px;
  color: hsl(0deg, 0%, 21%);
}
.textarea:hover, .input:hover {
  border-color: hsl(0deg, 0%, 71%);
}

.textarea, .input {
  box-shadow: inset 0 0.0625em 0.125em rgba(0, 0, 0, 0.05);
  max-width: 100%;
  width: 100%;
}
[readonly].textarea, [readonly].input {
  box-shadow: none;
}

.textarea {
  display: block;
  max-width: 100%;
  min-width: 100%;
  padding: calc(0.75em - 1px);
  resize: vertical;
}
.textarea:not([rows]) {
  max-height: 40em;
  min-height: 8em;
}
.textarea[rows] {
  height: initial;
}

.header {
  background-color: hsl(28deg, 38%, 85%);
  min-height: 3.25rem;
  position: relative;
  z-index: 30;
}
.header nav {
  position: relative;
  padding-top: 1.0625rem;
  padding-bottom: 1.0625rem;
}
.header__logo img {
  width: 8.8125rem;
  height: auto;
}
.header__toggle > span {
  display: block;
  width: 26px;
  height: 2px;
  background-color: hsl(0deg, 0%, 16%);
  transition: all 300ms ease-in-out;
  transform-origin: 3px 1px;
}
.header__toggle > span:not(:last-child) {
  margin-bottom: 5px;
}
.header__menu {
  position: absolute;
  width: calc(100% - 3rem);
  left: 50%;
  transform: translateX(-50%);
  background-color: #fff;
  margin-top: 1.5rem;
  padding: 1.625rem;
  border-radius: 5px;
}
.header__menu a {
  display: block;
  padding: 0.625rem;
  color: #000;
  text-align: center;
}
.header__links a {
  position: relative;
  font-size: 1rem;
  color: #000;
  transition: color 300ms ease-out;
}
.header__links a:not(:last-child) {
  margin-right: 32px;
}
.header__links a::before {
  content: "";
  display: block;
  position: absolute;
  height: 5px;
  left: 0;
  right: 0;
  bottom: -30px;
  background-color: hsl(15deg, 34%, 74%);
  opacity: 0;
  transition: opacity 300ms ease-out;
}
.header__links a:hover {
  color: hsl(0deg, 0%, 21%);
}
.header__links a:hover::before {
  opacity: 1;
}

.header__menu {
  display: none;
}

@media screen and (max-width: 63.9375em) {
  .header__menu {
    background-color: hsl(0deg, 0%, 100%);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.1);
    padding: 0.5rem 0;
  }
  .header__menu.toggle-active {
    display: block;
  }
}
